.container{
  margin: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.form{
  width:100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.input{
  width:50%;
  margin: 10px;
  padding: 10px;
  border-radius: 5px;
  resize: horizontal;
  font-family: Arial;
  font-size: 1em;
  height: 250px;
}

.input:focus, .input:active{
  border-color: #987284;
}

.output{
  height: 250px;
  background-color: #FFFFFF;
  border-radius: 5px;
  width: 50%;
  padding: 10px;
  margin: 10px;
  overflow-x: auto;
  border-width: 1px;
  border-color: #987284;
  border-style: solid;
}

@media (max-width: 500px){
  .form {
    -ms-flex-direction: column;
        flex-direction: column;
    width:100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
  .input, .output{
    width: 100%;
  }
}



